import { useState } from 'react'
import Select from 'react-select'
import Toggle from 'react-toggle'
import 'react-toggle/style.css'
import { Helmet } from 'react-helmet'

import { useCollections } from '../collections/CollectionsProvider'
import { LearningCard } from '../cards/LearningCard'

export default function Learning() {
  const [stateCollection, setCollection] = useState([])
  const [stateBlind, setBlind] = useState(false)
  const collections = useCollections()

  return (
    <>
      <Helmet>
        <title>正解 (Seikai) | Learning</title>
      </Helmet>
      <div className='w-full md:w-1/3 mx-auto'>
        <div className='flex flex-col justify-between items-center mb-8'>
          <div className='w-full'>
            <Select
              placeholder='Select collection...'
              options={ collections.index }
              onChange={(value) => { setCollection(collections.data[value.value]) }}
            />
          </div>
          <div className='flex items-center py-3 cursor-pointer'>
            <Toggle
              checked={stateBlind}
              className='mr-4'
              id='blindMode'
              icons={false}
              onChange={(event) => { setBlind(event.target.checked) }}
            />
            <label htmlFor='blindMode'>
              Blind mode
            </label>
          </div>
        </div>
        {
          stateCollection.length === 0 &&
            <div
              className='text-3xl italic text-center text-gray-400'
            >
              Please, select collection to learn
            </div>
        }
      </div>
      <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4'>
        {
          stateCollection.map((card, idx) => (
            <LearningCard
              key={idx}
              data={card}
              blind={stateBlind}
            />
          ))
        }
      </div>
    </>
  )
}