import cn from 'classnames'

function TrainingCard({
  data = null,
  checkAnswerHandler,
  dir = 'row'
}) {
  if (data === null) {
    return <div className='w-full text-center'>No data provided for card</div>
  }

  return (
    <div
      className='border border-white w-full rounded-xl overflow-hidden'
      style={{ height: '500px' }}
    >
      <div className={cn(
        'flex justify-center items-center bg-purple-100 text-purple-800',
        {
          'h-2/3': dir === 'row',
          'h-1/3': dir === 'col'
        }
      )}>
        { data.riddle }
      </div>
      <div className={cn(
        'grid border-t border-white',
        {
          'grid-flow-col auto-cols-fr h-1/3': dir === 'row',
          'grid-flow-row auto-rows-fr h-2/3': dir === 'col'
        }
      )}>
        {
          data.options.map((option, idx) => (
            <div
              key={ Math.random().toString() }
              className={cn(
                'flex justify-center items-center bg-indigo-100 hover:bg-indigo-200 text-indigo-900 cursor-pointer transition-all duration-300',
                { 'border-l border-white': idx > 0 && dir === 'row' },
                { 'border-t border-white': idx > 0 && dir === 'col' }
              )}
              onMouseUp={() => checkAnswerHandler(idx === data.indexOfCorrect, idx)}
            >
              { option }
            </div>    
          ))
        }
      </div>
    </div>
  )
}

export { TrainingCard }