import { shuffle } from './array'

export class CreateCard {
  constructor({ picks, mode, spelling }) {
    this.picks = picks
    this.mode = mode
    this.spelling = spelling
    this.modeRiddle = mode.split('-')[0]
    this.modeOptions = mode.split('-')[1]

    this.riddle = {}
    this.options = []
    this.indexOfCorrect = -1

    this.createData()
  }

  createData() {
    switch (this.modeRiddle) {
      case 'jp':
        this.riddle = {
          jp: this.picks[0].jp,
          onyomi: this.picks[0].onyomi?.[this.spelling],
          kunyomi: this.picks[0].kunyomi?.[this.spelling]
        }
      break

      case 'en':
        this.riddle = {
          en: this.picks[0].en,
          onyomi: this.picks[0].onyomi?.[this.spelling],
          kunyomi: this.picks[0].kunyomi?.[this.spelling]
        }
      break

      default: 
        this.riddle = {}
      break
    }

    switch (this.modeOptions) {
      case 'jp':
        this.options = shuffle(this.picks.map((pick, idx) => ({
          isCorrect: !idx,
          jp: pick.jp,
          onyomi: pick.onyomi?.[this.spelling],
          kunyomi: pick.kunyomi?.[this.spelling]
        })))
      break

      case 'en':
        this.options = shuffle(this.picks.map((pick, idx) => ({
          isCorrect: !idx,
          en: pick.en,
          onyomi: pick.onyomi?.[this.spelling],
          kunyomi: pick.kunyomi?.[this.spelling]
        })))
      break

      default:
        this.options = []
      break
    }

    this.indexOfCorrect = this.options.findIndex(opt => opt.isCorrect)
  }

  exportResultsCardElements() {
    let $riddle
    let $options

    switch (this.mode) {
      case 'jp-en':
        $riddle = 
          <span className='text-3xl'>
            { this.riddle.jp }
          </span>
        $options = this.options.map((opt, idx) => (
          <div className='w-full px-2 py-1'>
            <b>{ opt.en }</b>
            {
              (opt.onyomi !== undefined || opt.kunyomi !== undefined) &&
                <span className='text-sm'>
                  &nbsp;&nbsp;|&nbsp;&nbsp;
                  音読み:&nbsp;&nbsp;&nbsp;{ opt.onyomi || null }
                  &nbsp;&nbsp;|&nbsp;&nbsp;
                  訓読み:&nbsp;&nbsp;&nbsp;{ opt.kunyomi || null }
                </span>
            }
          </div>
        ))
      break
      
      case 'en-jp':
        $riddle =
          <b className='text-xl'>
            { this.riddle.en }
          </b>
        $options = this.options.map((opt, idx) => (
          <div className='w-full px-2 py-1'>
          { opt.jp }
          {
            (opt.onyomi !== undefined || opt.kunyomi !== undefined) &&
              <span className='text-sm'>
                &nbsp;&nbsp;|&nbsp;&nbsp;
                音読み:&nbsp;&nbsp;&nbsp;{ opt.onyomi || null }
                &nbsp;&nbsp;|&nbsp;&nbsp;
                訓読み:&nbsp;&nbsp;&nbsp;{ opt.kunyomi || null }
              </span>
          }
        </div>
        ))
      break
      
      default:
      break
    }

    return { 
      riddle: $riddle,
      options: $options,
      indexOfCorrect: this.indexOfCorrect
    }
  }

  exportTrainingCardElements() {
    let $riddle
    let $options

    switch (this.mode) {
      case 'jp-en':
        $riddle =
          <div className='text-5xl text-center'>
            { this.riddle.jp }
          </div>
        $options = this.options.map((opt, idx) => (
          <div className='w-full px-4'>
            <b>{ opt.en }</b>
            {
              (opt.onyomi !== undefined || opt.kunyomi !== undefined) &&
                <div className='text-sm'>
                  Onyomi:&nbsp;&nbsp;&nbsp;{ opt.onyomi || null }
                  <br/>
                  Kunyomi:&nbsp;&nbsp;&nbsp;{ opt.kunyomi || null }
                </div>
            }
          </div>
        ))
      break

      case 'en-jp':
        $riddle =
          <div className='w-full px-4'>
            <div className='mb-2 text-2xl text-center'>
              <b>{ this.riddle.en }</b>
            </div>
            {
              (this.riddle.onyomi !== undefined || this.riddle.kunyomi !== undefined) &&
                <>
                  Onyomi:&nbsp;&nbsp;&nbsp;{ this.riddle.onyomi || null }
                  <br/>
                  Kunyomi:&nbsp;&nbsp;&nbsp;{ this.riddle.kunyomi || null }
                </>
            }
          </div>
        $options = this.options.map((opt, idx) => (
          <div className='flex items-center text-4xl'>
            { opt.jp }
          </div>
        ))
      break

      default:
      break
    }
    
    return { 
      riddle: $riddle,
      options: $options,
      indexOfCorrect: this.indexOfCorrect
    }
  }

  exportTestingCardElements() {
    let $riddle
    let $options

    switch (this.mode) {
      case 'jp-en':
        $riddle =
          <div className='text-5xl text-center'>
            { this.riddle.jp }
          </div>
        $options = this.options.map((opt, idx) => (
          <div className='w-full px-4 py-1'>
            { opt.en }
            {
              (opt.onyomi !== undefined || opt.kunyomi !== undefined) &&
                <div className='w-full border-t border-white'>
                  音読み:&nbsp;{ opt.onyomi || null }
                  <br/>
                  訓読み:&nbsp;{ opt.kunyomi || null }
                </div>
            }
          </div>
        ))
      break

      case 'en-jp':
        $riddle =
          <div className='text-3xl text-center'>
            { this.riddle.en }
          </div>
        $options = this.options.map((opt, idx) => (
          <div className='w-full px-8'>
            { opt.jp }
            {
              ( opt.onyomi !== undefined || opt.kunyomi !== undefined) &&
                <div className='w-full border-t border-white'>
                  音読み:&nbsp;&nbsp;&nbsp;{ opt.onyomi || null }
                  <br/>
                  訓読み:&nbsp;&nbsp;&nbsp;{ opt.kunyomi || null }
                </div>
            }
          </div>
        ))
      break

      default:
      break
    }
    
    return { 
      riddle: $riddle,
      options: $options,
      indexOfCorrect: this.indexOfCorrect
    }
  }
}