import cn from 'classnames'
import { CreateCard } from '../helpers/card'

export function ResultsTable({
  cards = [],
  answers = [],
  dir='row'
}) {
  return (
    <div className='w-full border border-white rounded-xl overflow-hidden'>
      {
        cards.map((card, cardIdx) => {
          const { riddle, options, indexOfCorrect } = CreateCard.prototype.exportResultsCardElements.call(card)

          return (
            <div
              key={cardIdx}
              className={cn(
                'flex flex-wrap w-full text-indigo-900',
                {
                  'border-t-4 border-white': cardIdx,
                  'bg-indigo-100': cardIdx % 2 === 0,
                  'bg-indigo-200': cardIdx % 2 === 1
                }
              )}
            >
              <div
                className={cn(
                  'flex justify-center items-center w-full h-20 md:h-auto',
                  {
                    'md:w-1/2': dir === 'row',
                    'md:w-1/3': dir === 'col',
                  }
                )}
              >
                { riddle }
              </div>
              <div
                className={cn(
                  'grid w-full border-t md:border-t-0 md:border-l border-white',
                  {
                    'grid-flow-col auto-cols-fr md:w-1/2': dir === 'row',
                    'grid-flow-row auto-rows-fr md:w-2/3': dir === 'col'
                  }
                )}
              >
                {
                  options.map((option, optIdx) => (
                    <div
                      key={optIdx}
                      className={cn(
                        'flex justify-center items-center w-full h-20 md:h-auto',
                        {
                          'border-l border-white': optIdx && dir === 'row',
                          'border-t border-white': optIdx && dir === 'col',
                          'bg-green-200 text-green-700': indexOfCorrect === optIdx,
                          'bg-red-200 text-red-700': answers[cardIdx] === optIdx && indexOfCorrect !== optIdx
                        },
                      )}
                    >
                      { option }
                    </div>
                  ))
                }
              </div>
            </div>
          )
        })
      }
    </div>
  )
}